import { motion, useScroll, useTransform } from "framer-motion";
import { Download, ChevronDown } from "lucide-react";
import { ModernButton } from "./ModernButton";
import { useRef, useState, useEffect } from "react";
import { logDownload } from "../src/firebase";
import { isMobileDevice, prefersReducedMotion } from "../src/utils/deviceDetect";

export function ModernHero() {
  const containerRef = useRef<HTMLDivElement>(null);
  const [isMobile, setIsMobile] = useState(false);
  const [reducedMotion, setReducedMotion] = useState(false);

  useEffect(() => {
    setIsMobile(isMobileDevice());
    setReducedMotion(prefersReducedMotion());
  }, []);

  const { scrollYProgress } = useScroll({
    target: containerRef,
    offset: ["start start", "end start"],
  });

  const y = useTransform(scrollYProgress, [0, 1], ["0%", "50%"]);
  const opacity = useTransform(scrollYProgress, [0, 0.5], [1, 0]);

  // Simplified animation config for mobile
  const animationDuration = isMobile || reducedMotion ? 0.4 : 0.8;

  const handleGooglePlayClick = () => {
    logDownload('google_play', 'ViewFlow Browser');
  };

  const handleApkClick = () => {
    logDownload('apk', 'ViewFlow Browser');
  };

  return (
    <div ref={containerRef} className="relative min-h-screen flex items-center justify-center overflow-hidden">
      {/* Animated Grid Background */}
      <div className="absolute inset-0 opacity-30">
        <div
          className="absolute inset-0"
          style={{
            backgroundImage: `
              linear-gradient(to right, #00ccff22 1px, transparent 1px),
              linear-gradient(to bottom, #00ccff22 1px, transparent 1px)
            `,
            backgroundSize: "80px 80px",
          }}
        />
      </div>

      {/* Gradient Orbs - Simplified for mobile */}
      {!isMobile && !reducedMotion && (
        <>
          <motion.div
            className="absolute top-1/4 -left-1/4 w-[600px] h-[600px] bg-blue-600/30 rounded-full blur-[120px]"
            animate={{
              scale: [1, 1.2, 1],
              x: [0, 50, 0],
              y: [0, 30, 0],
            }}
            transition={{
              duration: 8,
              repeat: Infinity,
              ease: "easeInOut",
            }}
          />
          <motion.div
            className="absolute bottom-1/4 -right-1/4 w-[500px] h-[500px] bg-cyan-400/20 rounded-full blur-[120px]"
            animate={{
              scale: [1, 1.3, 1],
              x: [0, -30, 0],
              y: [0, 50, 0],
            }}
            transition={{
              duration: 10,
              repeat: Infinity,
              ease: "easeInOut",
            }}
          />
        </>
      )}
      {/* Static gradient orbs for mobile */}
      {(isMobile || reducedMotion) && (
        <>
          <div className="absolute top-1/4 -left-1/4 w-[600px] h-[600px] bg-blue-600/20 rounded-full blur-[120px]" />
          <div className="absolute bottom-1/4 -right-1/4 w-[500px] h-[500px] bg-cyan-400/15 rounded-full blur-[120px]" />
        </>
      )}

      <motion.div style={{ y, opacity }} className="relative z-10 container mx-auto px-6 text-center">
        {/* Main Title */}
        <motion.h1
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: animationDuration, delay: 0.1 }}
          className="text-6xl md:text-8xl font-bold mb-6 tracking-tight will-change-transform"
        >
          <span className="bg-gradient-to-r from-white via-cyan-200 to-cyan-400 bg-clip-text text-transparent">
            ViewFlow Browser
          </span>
        </motion.h1>

        {/* Subtitle */}
        <motion.p
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: animationDuration, delay: isMobile || reducedMotion ? 0.2 : 0.4 }}
          className="text-xl md:text-3xl text-slate-300 mb-4 max-w-3xl mx-auto will-change-transform"
        >
          Big Vision, One Touch Away
        </motion.p>

        <motion.p
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: animationDuration, delay: isMobile || reducedMotion ? 0.3 : 0.5 }}
          className="text-lg text-slate-400 mb-12 max-w-2xl mx-auto will-change-transform"
        >
          Experience the web like never before with remote-first navigation,
          intelligent content scaling, and seamless video playback
        </motion.p>

        {/* CTA Buttons */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: animationDuration, delay: isMobile || reducedMotion ? 0.4 : 0.6 }}
          className="flex flex-col sm:flex-row gap-4 justify-center items-center will-change-transform"
        >
          <a
            href="https://play.google.com/store/apps/details?id=com.viewflow.browser"
            target="_blank"
            rel="noopener noreferrer"
            onClick={handleGooglePlayClick}
          >
            <ModernButton
              size="lg"
              glow
              icon={<Download className="w-5 h-5" />}
            >
              Get on Google Play
            </ModernButton>
          </a>

          <a
            href="https://www.baidu.com/a.apk"
            target="_blank"
            rel="noopener noreferrer"
            onClick={handleApkClick}
          >
            <ModernButton
              size="lg"
              variant="outline"
              icon={<Download className="w-5 h-5" />}
            >
              Install APK
            </ModernButton>
          </a>
        </motion.div>

        {/* Stats - Commented out per requirements */}
        {/* <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.8 }}
          className="grid grid-cols-3 gap-8 max-w-2xl mx-auto mt-16"
        >
          {[
            { value: "500K+", label: "Downloads" },
            { value: "98%", label: "Satisfaction" },
            { value: "4.9", label: "Rating" },
          ].map((stat, index) => (
            <div key={index} className="text-center">
              <div className="text-3xl md:text-4xl font-bold text-cyan-400 mb-1">
                {stat.value}
              </div>
              <div className="text-sm text-slate-400">{stat.label}</div>
            </div>
          ))}
        </motion.div> */}
      </motion.div>

      {/* Scroll Indicator */}
      {!isMobile && !reducedMotion && (
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0.8 }}
          className="absolute bottom-8 left-1/2 -translate-x-1/2"
        >
          <motion.div
            animate={{ y: [0, 10, 0] }}
            transition={{ duration: 2, repeat: Infinity }}
            className="flex flex-col items-center gap-2 text-slate-400"
          >
            <span className="text-sm">Scroll to explore</span>
            <ChevronDown className="w-5 h-5" />
          </motion.div>
        </motion.div>
      )}
    </div>
  );
}