import { motion } from "framer-motion";

interface ViewFlowLogoProps {
  size?: "sm" | "md" | "lg";
  showText?: boolean;
  animated?: boolean;
}

export function ViewFlowLogo({ size = "md", showText = true, animated = true }: ViewFlowLogoProps) {
  const sizes = {
    sm: { icon: 28, text: "text-lg" },
    md: { icon: 36, text: "text-xl" },
    lg: { icon: 52, text: "text-3xl" },
  };

  const iconSize = sizes[size].icon;
  const textSize = sizes[size].text;

  return (
    <div className="flex items-center gap-3">
      {/* Logo Icon - Custom SVG Logo */}
      <motion.div
        whileHover={animated ? { scale: 1.1 } : {}}
        className="relative"
        style={{ width: iconSize, height: iconSize }}
      >
        <img
          src="/logo.svg"
          alt="ViewFlow Browser Logo"
          width={iconSize}
          height={iconSize}
          className="w-full h-full object-contain"
        />

        {/* Rotating Ring Glow */}
        {animated && (
          <motion.div
            className="absolute inset-0 rounded-full pointer-events-none"
            style={{
              background: "radial-gradient(circle, rgba(0,204,255,0.3) 0%, transparent 70%)",
            }}
            animate={{
              opacity: [0.3, 0.6, 0.3],
              scale: [0.9, 1.1, 0.9],
            }}
            transition={{
              duration: 3,
              repeat: Infinity,
              ease: "easeInOut",
            }}
          />
        )}
      </motion.div>

      {/* Logo Text */}
      {showText && (
        <motion.span
          initial={animated ? { opacity: 0, x: -10 } : {}}
          animate={{ opacity: 1, x: 0 }}
          transition={{ duration: 0.5, delay: 0.5 }}
          className={`${textSize} font-bold bg-gradient-to-r from-cyan-400 via-blue-400 to-cyan-500 bg-clip-text text-transparent tracking-tight`}
        >
          ViewFlow Browser
        </motion.span>
      )}
    </div>
  );
}
