import { useEffect } from "react";

interface SEOProps {
  title?: string;
  description?: string;
  keywords?: string;
  ogImage?: string;
  ogUrl?: string;
  twitterHandle?: string;
}

export function SEO({
  title = "ViewFlow -  | ",
  description = "ViewFlow ",
  keywords = ",,,ViewFlow,,TV Browser,Smart TV Browser,,,,Android TV,",
  ogImage = "https://images.unsplash.com/photo-1593359677879-a4bb92f829d1?w=1200&h=630&fit=crop",
  ogUrl = "https://viewflow.tv",
  twitterHandle = "@ViewFlowBrowser",
}: SEOProps) {
  useEffect(() => {
    // Set document title
    document.title = title;

    // Helper function to set or update meta tags
    const setMetaTag = (name: string, content: string, isProperty = false) => {
      const attribute = isProperty ? "property" : "name";
      let element = document.querySelector(`meta[${attribute}="${name}"]`);
      
      if (!element) {
        element = document.createElement("meta");
        element.setAttribute(attribute, name);
        document.head.appendChild(element);
      }
      
      element.setAttribute("content", content);
    };

    // Basic Meta Tags
    setMetaTag("description", description);
    setMetaTag("keywords", keywords);
    setMetaTag("author", "ViewFlow Team");
    setMetaTag("robots", "index, follow, max-image-preview:large, max-snippet:-1, max-video-preview:-1");
    setMetaTag("googlebot", "index, follow");
    setMetaTag("language", "zh-CN");
    setMetaTag("revisit-after", "7 days");

    // Viewport (usually in HTML but ensuring it's there)
    setMetaTag("viewport", "width=device-width, initial-scale=1.0, maximum-scale=5.0");

    // Theme Color
    setMetaTag("theme-color", "#0f172a");
    setMetaTag("msapplication-TileColor", "#0f172a");

    // Open Graph Meta Tags (for social media sharing)
    setMetaTag("og:title", title, true);
    setMetaTag("og:description", description, true);
    setMetaTag("og:image", ogImage, true);
    setMetaTag("og:url", ogUrl, true);
    setMetaTag("og:type", "website", true);
    setMetaTag("og:site_name", "ViewFlow", true);
    setMetaTag("og:locale", "zh_CN", true);

    // Twitter Card Meta Tags
    setMetaTag("twitter:card", "summary_large_image");
    setMetaTag("twitter:title", title);
    setMetaTag("twitter:description", description);
    setMetaTag("twitter:image", ogImage);
    setMetaTag("twitter:site", twitterHandle);
    setMetaTag("twitter:creator", twitterHandle);

    // Additional SEO tags
    setMetaTag("application-name", "ViewFlow");
    setMetaTag("apple-mobile-web-app-title", "ViewFlow");
    setMetaTag("apple-mobile-web-app-capable", "yes");
    setMetaTag("apple-mobile-web-app-status-bar-style", "black-translucent");
    setMetaTag("format-detection", "telephone=no");

    // Canonical URL
    let canonical = document.querySelector("link[rel='canonical']") as HTMLLinkElement;
    if (!canonical) {
      canonical = document.createElement("link");
      canonical.rel = "canonical";
      document.head.appendChild(canonical);
    }
    canonical.href = ogUrl;

    // Structured Data (JSON-LD for rich snippets)
    let structuredData = document.querySelector("script[type='application/ld+json']");
    if (!structuredData) {
      structuredData = document.createElement("script");
      structuredData.setAttribute("type", "application/ld+json");
      document.head.appendChild(structuredData);
    }

    const jsonLd = {
      "@context": "https://schema.org",
      "@type": "SoftwareApplication",
      "name": "ViewFlow",
      "applicationCategory": "BrowserApplication",
      "operatingSystem": "Android TV, Smart TV",
      "description": description,
      "url": ogUrl,
      "image": ogImage,
      "offers": {
        "@type": "Offer",
        "price": "0",
        "priceCurrency": "CNY"
      },
      "aggregateRating": {
        "@type": "AggregateRating",
        "ratingValue": "4.8",
        "ratingCount": "2847"
      },
      "author": {
        "@type": "Organization",
        "name": "ViewFlow Team",
        "url": ogUrl
      },
      "keywords": keywords,
      "featureList": [
        "",
        "",
        "",
        "",
        "",
        ""
      ]
    };

    structuredData.textContent = JSON.stringify(jsonLd);

    // Alternate language versions (if you plan to support multiple languages)
    let alternateLang = document.querySelector("link[rel='alternate'][hreflang='zh-CN']") as HTMLLinkElement;
    if (!alternateLang) {
      alternateLang = document.createElement("link");
      alternateLang.rel = "alternate";
      alternateLang.hreflang = "zh-CN";
      document.head.appendChild(alternateLang);
    }
    alternateLang.href = ogUrl;

  }, [title, description, keywords, ogImage, ogUrl, twitterHandle]);

  return null;
}
