// Error Tracking System (Crashlytics-like for Web)
import { logError } from "./analytics";

interface ErrorInfo {
  message: string;
  stack?: string;
  componentStack?: string;
  errorCode?: string;
  fatal?: boolean;
  timestamp: string;
  url: string;
  userAgent: string;
}

/**
 * Initialize global error tracking
 */
export const initializeErrorTracking = () => {
  if (typeof window === 'undefined') return;

  // Track uncaught JavaScript errors
  window.addEventListener('error', (event) => {
    const errorInfo: ErrorInfo = {
      message: event.message,
      stack: event.error?.stack,
      fatal: true,
      timestamp: new Date().toISOString(),
      url: window.location.href,
      userAgent: navigator.userAgent,
    };

    trackError(errorInfo);
    console.error(' Uncaught error:', errorInfo);
  });

  // Track unhandled promise rejections
  window.addEventListener('unhandledrejection', (event) => {
    const errorInfo: ErrorInfo = {
      message: event.reason?.message || String(event.reason),
      stack: event.reason?.stack,
      fatal: false,
      timestamp: new Date().toISOString(),
      url: window.location.href,
      userAgent: navigator.userAgent,
    };

    trackError(errorInfo);
    console.error(' Unhandled promise rejection:', errorInfo);
  });

  console.log(' Error tracking initialized');
};

/**
 * Track a custom error
 * @param errorInfo - Error information
 */
const trackError = (errorInfo: ErrorInfo) => {
  // Log to Firebase Analytics
  logError(errorInfo.message, errorInfo.errorCode, errorInfo.fatal);

  // Store in local storage for debugging (keep last 50 errors)
  try {
    const errors = getStoredErrors();
    errors.unshift(errorInfo);
    const recentErrors = errors.slice(0, 50);
    localStorage.setItem('firebase_errors', JSON.stringify(recentErrors));
  } catch (error) {
    console.error('Failed to store error:', error);
  }
};

/**
 * Get stored errors from local storage
 */
const getStoredErrors = (): ErrorInfo[] => {
  try {
    const stored = localStorage.getItem('firebase_errors');
    return stored ? JSON.parse(stored) : [];
  } catch (error) {
    return [];
  }
};

/**
 * Manually log an error
 * @param error - Error object or message
 * @param errorCode - Optional error code
 * @param fatal - Whether the error is fatal
 */
export const captureError = (
  error: Error | string,
  errorCode?: string,
  fatal: boolean = false
) => {
  const errorInfo: ErrorInfo = {
    message: typeof error === 'string' ? error : error.message,
    stack: typeof error === 'object' ? error.stack : undefined,
    errorCode,
    fatal,
    timestamp: new Date().toISOString(),
    url: window.location.href,
    userAgent: navigator.userAgent,
  };

  trackError(errorInfo);
  console.error(' Captured error:', errorInfo);
};

/**
 * Log a non-fatal error
 * @param message - Error message
 * @param errorCode - Optional error code
 */
export const captureMessage = (message: string, errorCode?: string) => {
  captureError(message, errorCode, false);
};

/**
 * Set custom error context
 * @param key - Context key
 * @param value - Context value
 */
export const setErrorContext = (key: string, value: any) => {
  try {
    const context = getErrorContext();
    context[key] = value;
    sessionStorage.setItem('error_context', JSON.stringify(context));
  } catch (error) {
    console.error('Failed to set error context:', error);
  }
};

/**
 * Get error context
 */
const getErrorContext = (): Record<string, any> => {
  try {
    const stored = sessionStorage.getItem('error_context');
    return stored ? JSON.parse(stored) : {};
  } catch (error) {
    return {};
  }
};

/**
 * Clear error context
 */
export const clearErrorContext = () => {
  try {
    sessionStorage.removeItem('error_context');
  } catch (error) {
    console.error('Failed to clear error context:', error);
  }
};

/**
 * Get recent errors for debugging
 * @param count - Number of errors to retrieve (default: 10)
 */
export const getRecentErrors = (count: number = 10): ErrorInfo[] => {
  return getStoredErrors().slice(0, count);
};

/**
 * Clear stored errors
 */
export const clearStoredErrors = () => {
  try {
    localStorage.removeItem('firebase_errors');
  } catch (error) {
    console.error('Failed to clear stored errors:', error);
  }
};
