// Firebase Analytics Utilities
import { logEvent, setCurrentScreen, setUserId, setUserProperties } from "firebase/analytics";
import { analytics } from "./config";

/**
 * Log a page view event
 * @param pageName - Name of the page being viewed
 * @param pageLocation - URL of the page (optional)
 */
export const logPageView = (pageName: string, pageLocation?: string) => {
  if (!analytics) return;

  try {
    logEvent(analytics, 'page_view', {
      page_title: pageName,
      page_location: pageLocation || window.location.href,
      page_path: window.location.pathname,
    });

    setCurrentScreen(analytics, pageName);
    console.log(' Page view logged:', pageName);
  } catch (error) {
    console.error('Failed to log page view:', error);
  }
};

/**
 * Log a custom event
 * @param eventName - Name of the event
 * @param eventParams - Additional parameters for the event
 */
export const logCustomEvent = (eventName: string, eventParams?: Record<string, any>) => {
  if (!analytics) return;

  try {
    logEvent(analytics, eventName, eventParams);
    console.log(' Event logged:', eventName, eventParams);
  } catch (error) {
    console.error('Failed to log event:', error);
  }
};

/**
 * Log a button click event
 * @param buttonName - Name/label of the button clicked
 * @param location - Location/section where button was clicked
 */
export const logButtonClick = (buttonName: string, location?: string) => {
  logCustomEvent('button_click', {
    button_name: buttonName,
    button_location: location,
  });
};

/**
 * Log a download event
 * @param downloadType - Type of download (e.g., 'apk', 'google_play')
 * @param itemName - Name of the item being downloaded
 */
export const logDownload = (downloadType: string, itemName?: string) => {
  logCustomEvent('download', {
    download_type: downloadType,
    item_name: itemName,
  });
};

/**
 * Log a navigation event
 * @param destination - Where the user is navigating to
 * @param method - Method of navigation (e.g., 'click', 'scroll')
 */
export const logNavigation = (destination: string, method?: string) => {
  logCustomEvent('navigation', {
    destination,
    method,
  });
};

/**
 * Log a section view event
 * @param sectionName - Name of the section viewed
 */
export const logSectionView = (sectionName: string) => {
  logCustomEvent('section_view', {
    section_name: sectionName,
  });
};

/**
 * Set user ID for analytics
 * @param userId - Unique identifier for the user
 */
export const setAnalyticsUserId = (userId: string) => {
  if (!analytics) return;

  try {
    setUserId(analytics, userId);
    console.log(' User ID set:', userId);
  } catch (error) {
    console.error('Failed to set user ID:', error);
  }
};

/**
 * Set user properties for analytics
 * @param properties - Object containing user properties
 */
export const setAnalyticsUserProperties = (properties: Record<string, any>) => {
  if (!analytics) return;

  try {
    setUserProperties(analytics, properties);
    console.log(' User properties set:', properties);
  } catch (error) {
    console.error('Failed to set user properties:', error);
  }
};

/**
 * Log an error event
 * @param errorMessage - Error message
 * @param errorCode - Error code (optional)
 * @param fatal - Whether the error is fatal (optional)
 */
export const logError = (errorMessage: string, errorCode?: string, fatal: boolean = false) => {
  logCustomEvent('error', {
    error_message: errorMessage,
    error_code: errorCode,
    fatal,
  });
};
