// Firebase Configuration and Initialization
import { initializeApp, FirebaseApp } from "firebase/app";
import { getAnalytics, Analytics, isSupported } from "firebase/analytics";
import { getPerformance, FirebasePerformance } from "firebase/performance";

// Your web app's Firebase configuration
const firebaseConfig = {
  apiKey: "AIzaSyChrci6BBmD3aRXUmWSdcr3SzsDdc3xp24",
  authDomain: "vf-site-fb2b3.firebaseapp.com",
  projectId: "vf-site-fb2b3",
  storageBucket: "vf-site-fb2b3.firebasestorage.app",
  messagingSenderId: "588168126505",
  appId: "1:588168126505:web:500a672d1c97371d833c38",
  measurementId: "G-4GSF2Z3HGG"
};

// Initialize Firebase
let app: FirebaseApp | null = null;
let analytics: Analytics | null = null;
let performance: FirebasePerformance | null = null;

try {
  app = initializeApp(firebaseConfig);

  // Initialize Analytics (only in browser environment)
  if (typeof window !== 'undefined' && app) {
    isSupported().then((supported) => {
      if (supported && app) {
        analytics = getAnalytics(app);
        console.log(' Firebase Analytics initialized');
      }
    }).catch((err) => {
      console.warn(' Firebase Analytics not supported:', err);
    });

    // Initialize Performance Monitoring
    try {
      if (app) {
        performance = getPerformance(app);
        console.log(' Firebase Performance Monitoring initialized');
      }
    } catch (err) {
      console.warn(' Firebase Performance Monitoring failed:', err);
    }
  }
} catch (error) {
  console.error(' Firebase initialization failed:', error);
}

export { app, analytics, performance };
export default app!;
