// Firebase Performance Monitoring Utilities
import { trace } from "firebase/performance";
import { performance } from "./config";

/**
 * Create and start a custom trace
 * @param traceName - Name of the trace
 * @returns Trace object with stop method
 */
export const startTrace = (traceName: string) => {
  if (!performance) {
    return {
      putAttribute: () => {},
      putMetric: () => {},
      stop: () => {},
    };
  }

  try {
    const customTrace = trace(performance, traceName);
    customTrace.start();
    console.log(' Performance trace started:', traceName);

    return {
      putAttribute: (name: string, value: string) => {
        customTrace.putAttribute(name, value);
      },
      putMetric: (name: string, value: number) => {
        customTrace.putMetric(name, value);
      },
      stop: () => {
        customTrace.stop();
        console.log(' Performance trace stopped:', traceName);
      },
    };
  } catch (error) {
    console.error('Failed to start trace:', error);
    return {
      putAttribute: () => {},
      putMetric: () => {},
      stop: () => {},
    };
  }
};

/**
 * Measure the duration of an async operation
 * @param traceName - Name of the trace
 * @param operation - Async function to measure
 * @returns Result of the operation
 */
export const measureAsync = async <T>(
  traceName: string,
  operation: () => Promise<T>
): Promise<T> => {
  const customTrace = startTrace(traceName);

  try {
    const result = await operation();
    customTrace.stop();
    return result;
  } catch (error) {
    customTrace.putAttribute('error', 'true');
    customTrace.stop();
    throw error;
  }
};

/**
 * Measure the duration of a synchronous operation
 * @param traceName - Name of the trace
 * @param operation - Function to measure
 * @returns Result of the operation
 */
export const measureSync = <T>(traceName: string, operation: () => T): T => {
  const customTrace = startTrace(traceName);

  try {
    const result = operation();
    customTrace.stop();
    return result;
  } catch (error) {
    customTrace.putAttribute('error', 'true');
    customTrace.stop();
    throw error;
  }
};

/**
 * Track page load performance
 */
export const trackPageLoad = () => {
  if (typeof window === 'undefined' || !window.performance) return;

  const pageLoadTrace = startTrace('page_load');

  // Wait for page to fully load
  if (document.readyState === 'complete') {
    recordPageLoadMetrics(pageLoadTrace);
  } else {
    window.addEventListener('load', () => {
      recordPageLoadMetrics(pageLoadTrace);
    });
  }
};

/**
 * Record page load metrics from Navigation Timing API
 */
const recordPageLoadMetrics = (pageLoadTrace: ReturnType<typeof startTrace>) => {
  try {
    const perfData = window.performance.timing;
    const pageLoadTime = perfData.loadEventEnd - perfData.navigationStart;
    const connectTime = perfData.responseEnd - perfData.requestStart;
    const renderTime = perfData.domComplete - perfData.domLoading;

    pageLoadTrace.putMetric('page_load_time', pageLoadTime);
    pageLoadTrace.putMetric('connect_time', connectTime);
    pageLoadTrace.putMetric('render_time', renderTime);
    pageLoadTrace.stop();

    console.log(' Page load metrics recorded:', {
      pageLoadTime,
      connectTime,
      renderTime,
    });
  } catch (error) {
    console.error('Failed to record page load metrics:', error);
    pageLoadTrace.stop();
  }
};

/**
 * Track component render performance
 * @param componentName - Name of the component
 * @returns Object with start and stop methods
 */
export const trackComponentRender = (componentName: string) => {
  const renderTrace = startTrace(`${componentName}_render`);

  return {
    start: () => {
      console.log(' Component render started:', componentName);
    },
    stop: () => {
      renderTrace.stop();
      console.log(' Component render completed:', componentName);
    },
  };
};
