// Device Detection and Performance Utilities

/**
 * Detect if the device is mobile
 */
export const isMobileDevice = (): boolean => {
  if (typeof window === 'undefined') return false;
  return /iPhone|iPad|iPod|Android/i.test(navigator.userAgent) || window.innerWidth < 768;
};

/**
 * Check if user prefers reduced motion
 */
export const prefersReducedMotion = (): boolean => {
  if (typeof window === 'undefined') return false;
  return window.matchMedia('(prefers-reduced-motion: reduce)').matches;
};

/**
 * Get optimized animation config based on device
 */
export const getAnimationConfig = () => {
  const isMobile = isMobileDevice();
  const reducedMotion = prefersReducedMotion();

  if (reducedMotion) {
    return {
      duration: 0,
      ease: "linear",
    };
  }

  if (isMobile) {
    return {
      duration: 0.3,
      ease: "easeOut",
    };
  }

  return {
    duration: 0.8,
    ease: "easeInOut",
  };
};

/**
 * Get simplified animation config for mobile
 */
export const getMobileAnimationConfig = () => {
  return isMobileDevice() || prefersReducedMotion()
    ? { duration: 0.3, ease: "linear" }
    : { duration: 0.8, ease: "easeInOut" };
};
